.contactdata {
    margin: 20px auto;
    width: 80% ;
    text-align: center; 
    -webkit-box-shadow: 1px 5px 3px rgb(119, 111, 111); 
            box-shadow: 1px 5px 3px rgb(119, 111, 111); 
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow: hidden;
    background-color: pink
}

.contactdata:hover {
    overflow: hidden;
    -webkit-transform: scale(0.987);
        -ms-transform: scale(0.987);
            transform: scale(0.987);
}

.contactdata::-webkit-input-placeholder {
    text-align: center;
}

.contactdata::-moz-placeholder {
    text-align: center;
}

.contactdata::-ms-input-placeholder {
    text-align: center;
}

.contactdata::placeholder {
    text-align: center;
}

@media (width:600px) {
    .contactdata{
        width: 500px;
    }
}


