.Button {
    background-color: transparent;
    border: none;
    color: white;
    outline: none;
    cursor: pointer;
    font: inherit;
    padding: 18px;
    margin: 10px;
    font-weight: bold;
    }

.Button:hover {
    -webkit-transform: scale(0.94);
        -ms-transform: scale(0.94);
            transform: scale(0.94);
    }

.Button:first-of-type {
    margin-left: 0;
    padding-left: 0;
    }

.Success {
    color: #395f04;
    }

.Danger {
    color: #803c18;
    }
    
.formSuccess {
    padding: 18px;
    color: rgb(233, 68, 95)
    }

.Button:disabled {
    color: rgb(90, 89, 89);
    cursor: not-allowed;
    }

.formSuccess:not(:disabled) {
    -webkit-animation: enable 0.5s linear;
            animation: enable 0.5s linear;
    }

@-webkit-keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}

@keyframes enable {
    0% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
    60% {
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
    }
    100% {
        -webkit-transform: scale(1);
                transform: scale(1);
    }
}


