.auth {
    margin: 20px auto;
    width: 50% ;
    text-align: center; 
    -webkit-box-shadow: 6px 10px 7px rgb(194, 129, 148); 
            box-shadow: 6px 10px 7px rgb(194, 129, 148); 
    padding: 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    overflow: hidden;
    background-color: rgb(202, 144, 174);
    border-radius: 2px;
}

.auth:hover {
    overflow: hidden;
    -webkit-transform: scale(0.987);
        -ms-transform: scale(0.987);
            transform: scale(0.987);
}

.auth::-webkit-input-placeholder {
    text-align: center;
}

.auth::-moz-placeholder {
    text-align: center;
}

.auth::-ms-input-placeholder {
    text-align: center;
}

.auth::placeholder {
    text-align: center;
}

@media (width:600px) {
    .auth{
        width: 500px;
    }
}


